<template>
<div id="wrapper" >
     <!-- <img :src="pic_url " id="bg" class="" alt=""> -->
     <section class="new_wrapper fullscreen overlay"  >
        <img @click="exitKioasMode()" src="static/emoji/grey_question.svg"  class="position_gear">
        <youtube :video-id="getVideoId()" :player-vars="playerVars" @playing="playing" class="bg_video index_class"></youtube>
         <!-- <img @click="exitKioasMode()" src="static/emoji/grey_question.svg"  class="position_gear"> -->
         <!-- <img  v-if="!online" src="static/offline.png"  class="position_offline"> -->
          <div class="question-overlay btn_container">
               <div class="inner text-center">
                  <h1>{{all_feedback[0]['feedback_title']}}</h1>
                  <p>{{all_feedback[0]['feedback_desc']}}</p>
                  <ul>
                     <li><a @click="goToSurvey()" class="button">Take Survey</a></li>
                  </ul>
              </div>
          </div>
      </section>
</div>
</template>
<script>
import Feedback from '../databases/feedback'
import myVideo from 'vue-video-module'
import { mapGetters } from 'vuex';
export default {
  mixins: [Feedback],
  components: {
        myVideo
  },
  data(){
    return {
        id: '',
        videoId: '',
        playerVars: {
          autoplay: 1,
          controls: 0,
          suggestedQuality: 'large'

        },
        feedback_video: '',
        hasVideo:false,
        pic_url: 'static/survey-themes/people-image.png',
        database: 'SurveyDb',
        version: '1.0',
        dbDisplay: 'ServeyDatabase',
        maxSize: 1105535,
        db: '',
         video: {
          sources: [{
              src: 'static/video/girl.mp4',
              type: 'video/mp4'
          }],
          options: {
            src: 'https://www.youtube.com/watch?v=Z6WIAbPyrQA',
            poster: 'http://www.freemake.com/blog/wp-content/uploads/2015/06/videojs-logo.jpg',
            spinner: 'circles',
            loop: 1,
            controlBar:false,
            autoPlay: true,
            fullscreen: false,
          }
      }
    }
  },
  watch: {
		'$route'(to, from) {
			// this.id = this.$route.params.id
		}
	},
  computed: {
     ...mapGetters([
        'all_feedback',
      ]),
  },
  created(){
     var db
     db = openDatabase(this.database, this.version, this.dbDisplay, this.maxSize)
     this.db = db
     this.id = this.$route.params.id
     this.getSurveyTitle()
     this.getIntroVideo(this.$route.params.id)
  },
  mounted() {

  },
  methods: {
     playing() {
      console.log('\o/ we are watching!!!')
    },
    getVideoId() {
      return this.$youtube.getIdFromUrl(this.videoId)
    },
     goToSurvey() {
         this.$router.push({name: 'Survey'})
      },
      exitKioasMode() {
        //  KioskPlugin.exitKiosk();
        //  localStorage.setItem("user_id", '');
         this.$router.push({name: 'Home'});
      },
      getIntroVideo(feedback_id) {
         this.$http.get('https://happyreply.com/api/get/video-link/'+feedback_id)
        .then(response => {
          this.videoId = response.body
        }).then(data => {
          console.log(data)
        })
      }
  }
}
</script>
<style>
 .index_class {
   z-index: -1000;
 }
 .video_container {
  width: 500px;
  height: 500px;
 }
 .inde{
   z-index: -1000;
 }
  #bg {
      position: fixed;
      /*background:linear-gradient(0deg,rgba(255,0,150,0.3),rgba(255,0,150,0.3)),url(http://lorempixel.com/800/600/nature/2);*/
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      /* Preserve aspet ratio */
      min-width: 50%;
      min-height: 50%;
  }

 .new_wrapper{
    width: 100%;
    height: 100vh;
    margin:0 auto;
    position: relative;
    overflow: hidden;
}

.new_wrapper .bg_video {
   position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
  z-index: -100;
  object-fit: cover;
}

.new_wrapper .btn_container {
  position: absolute;
  top: 50%;  /* position the top  edge of the element at the middle of the parent */
    left: 50%; /* position the left edge of the element at the middle of the parent */

    transform: translate(-50%, -50%);
  z-index: 100;
  object-fit: cover;
}

.mt-5 {
  margin-top: 2px;
}
.new_wrapper div {
    position: relative;
    z-index: 1;
}

    .position_gear{
        cursor: pointer;
        background: #fff;
        border-radius: 10px;
        position: absolute;
        top: 10px;
        right: 10px;
        padding:2px;
        color: #fff;
        font-weight: 50px;
        width:20px;
        height:20px;
        z-index: 1000;
    }
</style>




